/**
 * Ê∏∏ÊàèÁä∂ÊÄÅÁÆ°ÁêÜÂô®
 * Ë¥üË¥£ÁÆ°ÁêÜÊ∏∏ÊàèÁöÑÂêÑÁßçÁä∂ÊÄÅÔºåÂåÖÊã¨ÂºÄÂßã„ÄÅÊöÇÂÅú„ÄÅÁªìÊùüÁ≠â
 */

export enum GameState {
    LOADING = 'loading',
    MENU = 'menu', 
    PLAYING = 'playing',
    PAUSED = 'paused',
    GAME_OVER = 'game_over',
    VICTORY = 'victory'
}

export interface GameData {
    currentScore: number;
    targetScore: number;
    moveCount: number;
    remainingBlocks: number;
    elapsedTime: number;
    level: number;
}

export interface GameStatistics {
    totalMoves: number;
    totalScore: number;
    largestElimination: number;
    averageScorePerMove: number;
    playTime: number;
    consecutiveVictories: number;
}

export class GameStateManager {
    
    // ÂΩìÂâçÊ∏∏ÊàèÁä∂ÊÄÅ
    private currentState: GameState = GameState.LOADING;
    private previousState: GameState = GameState.LOADING;
    
    // Ê∏∏ÊàèÊï∞ÊçÆ
    private gameData: GameData = {
        currentScore: 0,
        targetScore: 1000,
        moveCount: 0,
        remainingBlocks: 100,
        elapsedTime: 0,
        level: 1
    };
    
    // Ê∏∏ÊàèÁªüËÆ°
    private gameStatistics: GameStatistics = {
        totalMoves: 0,
        totalScore: 0,
        largestElimination: 0,
        averageScorePerMove: 0,
        playTime: 0,
        consecutiveVictories: 0
    };
    
    // Êó∂Èó¥Ë∑üË∏™
    private gameStartTime: number = 0;
    private pauseStartTime: number = 0;
    private totalPausedTime: number = 0;
    
    // Áä∂ÊÄÅÂèòÂåñÂõûË∞É
    private stateChangeCallbacks: Map<GameState, (() => void)[]> = new Map();
    
    /**
     * ÂàùÂßãÂåñÊ∏∏ÊàèÁä∂ÊÄÅÁÆ°ÁêÜÂô®
     */
    init() {
        this.initializeStateCallbacks();
        this.loadGameStatistics();
        
        console.log('‚úÖ Ê∏∏ÊàèÁä∂ÊÄÅÁÆ°ÁêÜÂô®ÂàùÂßãÂåñÂÆåÊàê');
    }
    
    /**
     * ÂàùÂßãÂåñÁä∂ÊÄÅÂõûË∞ÉÊò†Â∞Ñ
     */
    private initializeStateCallbacks() {
        for (const state of Object.values(GameState)) {
            this.stateChangeCallbacks.set(state, []);
        }
    }
    
    /**
     * ËÆæÁΩÆÊ∏∏ÊàèÁä∂ÊÄÅ
     */
    setState(newState: GameState) {
        if (this.currentState === newState) {
            console.log(`‚ö†Ô∏è Ê∏∏ÊàèÁä∂ÊÄÅÂ∑≤ÁªèÊòØ ${newState}ÔºåË∑≥ËøáËÆæÁΩÆ`);
            return;
        }
        
        console.log(`üîÑ Ê∏∏ÊàèÁä∂ÊÄÅÂèòÂåñ: ${this.currentState} -> ${newState}`);
        
        // Â§ÑÁêÜÁä∂ÊÄÅÈÄÄÂá∫ÈÄªËæë
        this.handleStateExit(this.currentState);
        
        // Êõ¥Êñ∞Áä∂ÊÄÅ
        this.previousState = this.currentState;
        this.currentState = newState;
        
        // Â§ÑÁêÜÁä∂ÊÄÅËøõÂÖ•ÈÄªËæë
        this.handleStateEnter(newState);
        
        // Ë∞ÉÁî®Áä∂ÊÄÅÂèòÂåñÂõûË∞É
        this.triggerStateCallbacks(newState);
    }
    
    /**
     * Â§ÑÁêÜÁä∂ÊÄÅÈÄÄÂá∫ÈÄªËæë
     */
    private handleStateExit(state: GameState) {
        switch (state) {
            case GameState.PLAYING:
                // ËÆ∞ÂΩïÊ∏∏ÊàèÊó∂Èó¥
                if (this.gameStartTime > 0) {
                    this.gameData.elapsedTime = Date.now() - this.gameStartTime - this.totalPausedTime;
                }
                break;
                
            case GameState.PAUSED:
                // ËÆ∞ÂΩïÊöÇÂÅúÊó∂Èó¥
                if (this.pauseStartTime > 0) {
                    this.totalPausedTime += Date.now() - this.pauseStartTime;
                    this.pauseStartTime = 0;
                }
                break;
        }
    }
    
    /**
     * Â§ÑÁêÜÁä∂ÊÄÅËøõÂÖ•ÈÄªËæë
     */
    private handleStateEnter(state: GameState) {
        switch (state) {
            case GameState.PLAYING:
                if (this.gameStartTime === 0) {
                    this.gameStartTime = Date.now();
                    this.totalPausedTime = 0;
                }
                break;
                
            case GameState.PAUSED:
                this.pauseStartTime = Date.now();
                break;
                
            case GameState.GAME_OVER:
            case GameState.VICTORY:
                this.finalizeGameSession();
                break;
        }
    }
    
    /**
     * ÁªìÊùüÊ∏∏Êàè‰ºöËØùÔºåÊõ¥Êñ∞ÁªüËÆ°Êï∞ÊçÆ
     */
    private finalizeGameSession() {
        // Êõ¥Êñ∞Ê∏∏ÊàèÁªüËÆ°
        this.gameStatistics.totalMoves += this.gameData.moveCount;
        this.gameStatistics.totalScore += this.gameData.currentScore;
        this.gameStatistics.playTime += this.gameData.elapsedTime;
        
        if (this.gameData.moveCount > 0) {
            this.gameStatistics.averageScorePerMove = 
                this.gameStatistics.totalScore / this.gameStatistics.totalMoves;
        }
        
        if (this.currentState === GameState.VICTORY) {
            this.gameStatistics.consecutiveVictories++;
        } else {
            this.gameStatistics.consecutiveVictories = 0;
        }
        
        // ‰øùÂ≠òÁªüËÆ°Êï∞ÊçÆ
        this.saveGameStatistics();
        
        console.log('üìä Ê∏∏Êàè‰ºöËØùÁªìÊùüÔºåÁªüËÆ°Êï∞ÊçÆÂ∑≤Êõ¥Êñ∞');
    }
    
    /**
     * ÂºÄÂßãÊñ∞Ê∏∏Êàè
     */
    startNewGame(level: number = 1, targetScore: number = 1000) {
        console.log(`üéÆ ÂºÄÂßãÊñ∞Ê∏∏Êàè - ÂÖ≥Âç°: ${level}, ÁõÆÊ†áÂàÜÊï∞: ${targetScore}`);
        
        // ÈáçÁΩÆÊ∏∏ÊàèÊï∞ÊçÆ
        this.gameData = {
            currentScore: 0,
            targetScore: targetScore,
            moveCount: 0,
            remainingBlocks: 100, // 10x10 Ê£ãÁõò
            elapsedTime: 0,
            level: level
        };
        
        // ÈáçÁΩÆÊó∂Èó¥Ë∑üË∏™
        this.gameStartTime = 0;
        this.pauseStartTime = 0;
        this.totalPausedTime = 0;
        
        // ËÆæÁΩÆ‰∏∫Ê∏∏Êàè‰∏≠Áä∂ÊÄÅ
        this.setState(GameState.PLAYING);
    }
    
    /**
     * ÊöÇÂÅúÊ∏∏Êàè
     */
    pauseGame() {
        if (this.currentState === GameState.PLAYING) {
            this.setState(GameState.PAUSED);
        }
    }
    
    /**
     * ÊÅ¢Â§çÊ∏∏Êàè
     */
    resumeGame() {
        if (this.currentState === GameState.PAUSED) {
            this.setState(GameState.PLAYING);
        }
    }
    
    /**
     * ÁªìÊùüÊ∏∏Êàè
     */
    endGame(isVictory: boolean = false) {
        const newState = isVictory ? GameState.VICTORY : GameState.GAME_OVER;
        this.setState(newState);
    }
    
    /**
     * Êõ¥Êñ∞Ê∏∏ÊàèÊï∞ÊçÆ
     */
    updateGameData(updates: Partial<GameData>) {
        Object.assign(this.gameData, updates);
        
        // Ê£ÄÊü•ËÉúÂà©Êù°‰ª∂
        if (updates.currentScore !== undefined && 
            this.gameData.currentScore >= this.gameData.targetScore) {
            this.endGame(true);
        }
        
        // Ê£ÄÊü•Â§±Ë¥•Êù°‰ª∂ÔºàÂ¶ÇÊûúÈúÄË¶ÅÁöÑËØùÔºâ
        // ‰æãÂ¶ÇÔºöÊó†ÂèØÊ∂àÈô§ÊñπÂùó‰∏îÂàÜÊï∞‰∏çË∂≥
    }
    
    /**
     * ËÆ∞ÂΩïÊ∂àÈô§Êìç‰Ωú
     */
    recordElimination(blocksEliminated: number, scoreGained: number) {
        this.gameData.moveCount++;
        this.gameData.currentScore += scoreGained;
        this.gameData.remainingBlocks -= blocksEliminated;
        
        // Êõ¥Êñ∞ÊúÄÂ§ßÊ∂àÈô§ËÆ∞ÂΩï
        if (blocksEliminated > this.gameStatistics.largestElimination) {
            this.gameStatistics.largestElimination = blocksEliminated;
        }
        
        console.log(`üìù ËÆ∞ÂΩïÊ∂àÈô§: ${blocksEliminated} ‰∏™ÊñπÂùó, +${scoreGained} ÂàÜ`);
    }
    
    /**
     * Ëé∑ÂèñÂΩìÂâçÊ∏∏ÊàèÁä∂ÊÄÅ
     */
    getCurrentState(): GameState {
        return this.currentState;
    }
    
    /**
     * Ëé∑Âèñ‰∏ä‰∏Ä‰∏™Ê∏∏ÊàèÁä∂ÊÄÅ
     */
    getPreviousState(): GameState {
        return this.previousState;
    }
    
    /**
     * Ëé∑ÂèñÊ∏∏ÊàèÊï∞ÊçÆ
     */
    getGameData(): GameData {
        return { ...this.gameData };
    }
    
    /**
     * Ëé∑ÂèñÊ∏∏ÊàèÁªüËÆ°
     */
    getGameStatistics(): GameStatistics {
        return { ...this.gameStatistics };
    }
    
    /**
     * Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•ËøõË°åÊ∏∏ÊàèÊìç‰Ωú
     */
    canPerformGameAction(): boolean {
        return this.currentState === GameState.PLAYING;
    }
    
    /**
     * Ê£ÄÊü•Ê∏∏ÊàèÊòØÂê¶ÁªìÊùü
     */
    isGameEnded(): boolean {
        return this.currentState === GameState.GAME_OVER || 
               this.currentState === GameState.VICTORY;
    }
    
    /**
     * Ê£ÄÊü•Ê∏∏ÊàèÊòØÂê¶ÊöÇÂÅú
     */
    isGamePaused(): boolean {
        return this.currentState === GameState.PAUSED;
    }
    
    /**
     * Ê≥®ÂÜåÁä∂ÊÄÅÂèòÂåñÂõûË∞É
     */
    onStateChange(state: GameState, callback: () => void) {
        const callbacks = this.stateChangeCallbacks.get(state);
        if (callbacks) {
            callbacks.push(callback);
        }
    }
    
    /**
     * ÁßªÈô§Áä∂ÊÄÅÂèòÂåñÂõûË∞É
     */
    offStateChange(state: GameState, callback: () => void) {
        const callbacks = this.stateChangeCallbacks.get(state);
        if (callbacks) {
            const index = callbacks.indexOf(callback);
            if (index > -1) {
                callbacks.splice(index, 1);
            }
        }
    }
    
    /**
     * Ëß¶ÂèëÁä∂ÊÄÅÂèòÂåñÂõûË∞É
     */
    private triggerStateCallbacks(state: GameState) {
        const callbacks = this.stateChangeCallbacks.get(state);
        if (callbacks) {
            callbacks.forEach(callback => {
                try {
                    callback();
                } catch (error) {
                    console.error(`‚ùå Áä∂ÊÄÅÂõûË∞ÉÊâßË°åÂá∫Èîô: ${error}`);
                }
            });
        }
    }
    
    /**
     * Ëé∑ÂèñÊ∏∏ÊàèËøõÂ∫¶ÁôæÂàÜÊØî
     */
    getGameProgress(): number {
        if (this.gameData.targetScore <= 0) return 0;
        return Math.min(100, (this.gameData.currentScore / this.gameData.targetScore) * 100);
    }
    
    /**
     * Ëé∑ÂèñÊ∏∏ÊàèÊïàÁéáËØÑÁ∫ß
     */
    getEfficiencyRating(): string {
        if (this.gameData.moveCount === 0) return "Êú™ÂºÄÂßã";
        
        const avgScore = this.gameData.currentScore / this.gameData.moveCount;
        
        if (avgScore >= 20) return "‰ºòÁßÄ";
        if (avgScore >= 10) return "ËâØÂ•Ω";
        if (avgScore >= 5) return "‰∏ÄËà¨";
        return "ÈúÄË¶ÅÊîπËøõ";
    }
    
    /**
     * ‰øùÂ≠òÊ∏∏ÊàèÁªüËÆ°Âà∞Êú¨Âú∞Â≠òÂÇ®
     */
    private saveGameStatistics() {
        try {
            if (typeof localStorage !== 'undefined') {
                localStorage.setItem('starElimination_statistics', JSON.stringify(this.gameStatistics));
                console.log('üíæ Ê∏∏ÊàèÁªüËÆ°Â∑≤‰øùÂ≠ò');
            }
        } catch (error) {
            console.warn('‚ö†Ô∏è ‰øùÂ≠òÊ∏∏ÊàèÁªüËÆ°Â§±Ë¥•:', error);
        }
    }
    
    /**
     * ‰ªéÊú¨Âú∞Â≠òÂÇ®Âä†ËΩΩÊ∏∏ÊàèÁªüËÆ°
     */
    private loadGameStatistics() {
        try {
            if (typeof localStorage !== 'undefined') {
                const saved = localStorage.getItem('starElimination_statistics');
                if (saved) {
                    this.gameStatistics = JSON.parse(saved);
                    console.log('üìÅ Ê∏∏ÊàèÁªüËÆ°Â∑≤Âä†ËΩΩ');
                }
            }
        } catch (error) {
            console.warn('‚ö†Ô∏è Âä†ËΩΩÊ∏∏ÊàèÁªüËÆ°Â§±Ë¥•:', error);
        }
    }
    
    /**
     * ÈáçÁΩÆÊ∏∏ÊàèÁªüËÆ°
     */
    resetStatistics() {
        this.gameStatistics = {
            totalMoves: 0,
            totalScore: 0,
            largestElimination: 0,
            averageScorePerMove: 0,
            playTime: 0,
            consecutiveVictories: 0
        };
        
        this.saveGameStatistics();
        console.log('üîÑ Ê∏∏ÊàèÁªüËÆ°Â∑≤ÈáçÁΩÆ');
    }
    
    /**
     * Ë∞ÉËØïÔºöÊâìÂç∞ÂΩìÂâçÁä∂ÊÄÅ
     */
    debugPrintState() {
        console.log('üé≤ Ê∏∏ÊàèÁä∂ÊÄÅ‰ø°ÊÅØ:');
        console.log(`  ÂΩìÂâçÁä∂ÊÄÅ: ${this.currentState}`);
        console.log(`  ‰∏ä‰∏ÄÁä∂ÊÄÅ: ${this.previousState}`);
        console.log(`  Ê∏∏ÊàèÊï∞ÊçÆ:`, this.gameData);
        console.log(`  Ê∏∏ÊàèÁªüËÆ°:`, this.gameStatistics);
        console.log(`  Ê∏∏ÊàèËøõÂ∫¶: ${this.getGameProgress().toFixed(1)}%`);
        console.log(`  ÊïàÁéáËØÑÁ∫ß: ${this.getEfficiencyRating()}`);
    }
}
